import React from 'react';
import { Form, Button, Container } from "react-bootstrap";
import { useForm } from "react-hook-form";
import API from "../utils/API";
import axios from "axios";

function register() {

    const { register, handleSubmit, errors, reset } = useForm();

    const onSubmit = data => {
        console.log(data)
        axios.post(' http://localhost:3001/api/signup', { data })
            .then((response) => {
                console.log(response);

            })
            .catch((err) => {
                console.log(err);

            });
        reset();
    }

    return (
        <Container style={{ width: "50%", marginTop: 50 }}>
            <h3>User Register Form</h3>
            <Form style={{ marginTop: 50 }} onSubmit={handleSubmit(onSubmit)}>
                <Form.Group controlId="formBasicEmail">
                    <Form.Label>User Name</Form.Label>
                    <Form.Control type="name" placeholder="Enter User Name" name="username" ref={register} />
                </Form.Group>
                <Form.Group controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" placeholder="Password" name="password" ref={register} />
                </Form.Group>
                <Button variant="primary" type="submit">
                    Submit
                </Button>
            </Form>
        </Container>
    )
}

export default register
